import compact from 'lodash/compact'
import PropTypes from 'prop-types'
import { useCallback, useContext, useMemo, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import Icon from '../../Common/Icon'
import styles from './layer-switcher.module.css'
import { stop } from '../../event-util'
import {
  selectLayers,
  createLayer,
  repositionLayer,
  deleteLayer
} from './layersSlice'
import { LayoutContext } from '../../Contexts/Layout'
import { layerIndexChanged } from './layer-index-change-action'
import { SearchContext } from '../../providers'
import { DefinitionsContext } from '../../Contexts/Definitions'

function LayerSwitcher ({ activeLayer, onSelect }) {
  const ref = useRef(null)
  const dispatch = useDispatch()
  const { bindingCount } = useContext(LayoutContext)
  const { sources } = useContext(SearchContext)
  const { behaviors } = useContext(DefinitionsContext)
  const layers = useSelector(selectLayers)

  const handleSelect = useCallback(function (layerIndex) {
    onSelect(layerIndex)
  }, [onSelect])

  const handleAdd = useCallback(() => dispatch(createLayer({
    bindingCount
  })), [dispatch, bindingCount])

  const handleDelete = useMemo(() => function (layerIndex) {
    const { name } = layers[layerIndex]
    if (window.confirm(`Really delete layer: ${name}?`)) {
      dispatch(deleteLayer({ index: layerIndex }))
      dispatch(layerIndexChanged(layerIndex, null, layers, sources, behaviors))
    }
  }, [layers, dispatch, sources, behaviors])

  const handleDrop = useCallback(function (event, dragState) {
    event.stopPropagation()
    const {
      dragIndex: currentIndex,
      dropIndex: targetIndex
    } = dragState

    if (currentIndex === targetIndex) {
      return
    }

    dispatch(repositionLayer({ currentIndex, targetIndex }))
    dispatch(layerIndexChanged(currentIndex, targetIndex, layers, sources, behaviors))
  }, [layers, dispatch, sources, behaviors])

  const [dragState, dragHandlers] = useDragAndDrop(handleDrop)

  return (
    <div
      className={styles.layerSelector}
      ref={ref}
    >
      <ul data-dragging={dragState.isDragging}>
        {layers.map((layer, i) => (
          <li
            key={`slot-${i}`}
            className={activeLayer === i ? styles.active : ''}
            data-slot-index={i}
            draggable
            onDragStart={dragHandlers.handleDragStart}
            onDragEnd={dragHandlers.handleDragEnd}
            onDrop={dragHandlers.handleDrop}
          >
            <div
              className={compact([
                styles.dropper,
                dragState.dropIndex === i && styles.dropTarget
              ]).join(' ')}
              data-drop-position={i}
              onDragEnter={dragHandlers.handleDragEnter}
              onDragOver={dragHandlers.handleDragOver}
              onDragLeave={dragHandlers.handleDragLeave}
              onDrop={dragHandlers.handleDrop}
            />
            <div
              onClick={() => handleSelect(i)}
              className={compact([
                styles.layer,
                dragState.dragIndex === i && styles.dragging,
                dragState.isDragging &&
                dragState.dropIndex !== dragState.dragIndex &&
                dragState.dropIndex !== dragState.dragIndex + 1 &&
                dragState.dropIndex !== null &&
                i >= dragState.dropIndex &&
                styles.displaced
              ]).join(' ')}
            >
              <span className={styles.index}>{i}</span>
              <span className={styles.name}>
                {layer.name}
                <Icon
                  name="times-circle"
                  className={styles.delete}
                  onClick={stop(() => handleDelete(i))}
                />
              </span>
            </div>
          </li>
        ))}
        {dragState.isDragging && dragState.dragIndex < layers.length - 1 && (
          <li
            key={`slot-${layers.length}`}
            data-slot-index={layers.length}
            onDragEnd={dragHandlers.handleDragEnd}
            onDrop={dragHandlers.handleDrop}
          >
            <div
              className={compact([
                styles.dropper,
                dragState.dropIndex === layers.length && styles.dropTarget
              ]).join(' ')}
              data-drop-position={layers.length}
              onDragEnter={dragHandlers.handleDragEnter}
              onDragOver={dragHandlers.handleDragOver}
              onDragLeave={dragHandlers.handleDragLeave}
              onDrop={dragHandlers.handleDrop}
            />
          </li>
        )}
        {!dragState.isDragging && (
          <li onClick={handleAdd}>
            <div className={styles.layer}>
              <Icon className={styles.index} name="plus" />
              <span className={styles.name}>Add Layer</span>
            </div>
          </li>
        )}
      </ul>
    </div>
  )
}

function useDragAndDrop (onDrop) {
  const [{ dragIndex, dropIndex }, setDragState] = useState({ dragIndex: null, dropIndex: null })

  const handleDragOver = useCallback(event => event.preventDefault(), [])

  const handleDragStart = useCallback(function (event) {
    const dragIndex = Number(event.target.dataset.slotIndex)
    setDragState({ dragIndex, dropIndex: null })
  }, [setDragState])

  const handleDragEnd = useCallback(function (event) {
    setDragState(state => {
      if (state.dropIndex === null) {
        event.stopPropagation()
      }

      return { dragIndex: null, dropIndex: null }
    })
  }, [setDragState])

  const handleDragEnter = useCallback(function (event) {
    event.preventDefault()
    const dropIndex = Number(event.target.dataset.dropPosition)
    setDragState(state => ({ ...state, dropIndex }))
  }, [setDragState])

  const handleDragLeave = useCallback(function (event) {
    const dropIndex = Number(event.target.dataset.dropPosition)
    setDragState(state => ({
      ...state, dropIndex: state.dropIndex === dropIndex ? null : state.dropIndex
    }))
  }, [setDragState])

  const handleDrop = useCallback(function (event) {
    onDrop(event, { dragIndex, dropIndex })
    setDragState({ dragIndex: null, dropIndex: null })
  }, [dragIndex, dropIndex, setDragState, onDrop])

  const isDragging = dragIndex !== null

  return [
    { isDragging, dragIndex, dropIndex },
    {
      handleDragStart,
      handleDragEnd,
      handleDragEnter,
      handleDragLeave,
      handleDragOver,
      handleDrop
    }
  ]
}

LayerSwitcher.propTypes = {
  activeLayer: PropTypes.number.isRequired,
  onSelect: PropTypes.func.isRequired
}

export default LayerSwitcher
