import PropTypes from 'prop-types'

import Binding from '../Binding/Binding'
import * as bindPropTypes from '../Binding/bindPropTypes'

import KeyWrapper from '../Keys/KeyWrapper'
import styles from '../Keys/styles.module.css'

function EncoderBindings (props) {
  const { context, binding, onUpdate } = props
  const { value, params } = binding

  return (
    <KeyWrapper themeClass={styles.encoder2}>
      <Binding
        context={context}
        value={value}
        params={params}
        onUpdate={onUpdate}
      />
    </KeyWrapper>
  )
}

EncoderBindings.propTypes = {
  binding: bindPropTypes.node,
  onUpdate: PropTypes.func.isRequired
}

export default EncoderBindings
