import { useState } from 'react'
import { useSelector } from 'react-redux'

import Layer from './Layer'
import LayerSwitcher from './LayerSwitcher'
import { selectLayers } from './layersSlice'

import PromptForFirstLayer from './PromptForFirstLayer'
// import LayerScroller from './LayerScroller'

const KeymapEditor = () => {
  const layers = useSelector(selectLayers)
  const [activeLayer, setActiveLayer] = useState(0)

  const clampedActiveLayer = Math.max(0, Math.min(activeLayer, layers.length - 1))

  if (layers.length === 0) {
    return <PromptForFirstLayer />
  }

  return (
    <>
      <LayerSwitcher
        activeLayer={clampedActiveLayer}
        onSelect={setActiveLayer}
      />
      {/* <LayerScroller>
        {layers.map((layer, i) => (
          <Layer
            key={i}
            layer={layer}
            layerIndex={i}
            isActiveLayer={i === clampedActiveLayer}
          />
        ))}
      </LayerScroller> */}
      <Layer
        layer={layers[clampedActiveLayer]}
        layerIndex={clampedActiveLayer}
        isActiveLayer={true}
      />
    </>
  )
}

export default KeymapEditor
