import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import Input from '../../Common/Input'
import Modal from '../../Common/Modal'
import DialogBox from '../../Common/DialogBox'
import styles from './style.module.css'
import { validateDevicetreeNodeName } from '../Common/Devicetree/validators'
import ActionButton from '../../Common/ActionButton'
import { selectLayers, updateLayerMetadata } from './layersSlice'

function ConfigModal ({ layerIndex, onClose }) {
  const dispatch = useDispatch()
  const layers = useSelector(selectLayers)
  const layer = layers[layerIndex]

  const [pending, setPending] = useState({
    name: layer.name,
    label: layer.properties.label?.parsed || ''
  })

  function handleConfirm () {
    dispatch(updateLayerMetadata({ layerIndex, ...pending }))
    onClose()
  }

  return (
    <Modal onDismiss={onClose}>
      <DialogBox dismissText="">
        <h2>Customize Layer <code>{layer.name}</code></h2>
        <div>

          <div className={styles.field}>
            <label htmlFor="customize-layer-name">
              Name
            </label>
            <Input
              id="customize-layer-name"
              placeholder="Default"
              value={pending.name}
              validator={validateDevicetreeNodeName}
              onChange={event => (
                setPending(state => ({
                  ...state,
                  name: event.target.value
                }))
              )}
            />
          </div>

          <div className={styles.field}>
            <label htmlFor="customize-layer-label">
              Label
            </label>
            <input
              id="customize-layer-label"
              placeholder="Default"
              value={pending.label}
              onChange={event => (
                setPending(state => ({
                  ...state,
                  label: event.target.value
                }))
              )}
            />
          </div>

        </div>
        <div style={{ textAlign: 'center', gap: '5px' }}>
          <ActionButton onClick={handleConfirm}>OK</ActionButton>
          <ActionButton onClick={onClose} secondary>Cancel</ActionButton>
        </div>
      </DialogBox>
    </Modal>
  )
}

export default ConfigModal
