import PropTypes from 'prop-types'

import Placer from './KeyPlacer'
import * as layoutPropTypes from './layoutPropTypes'

function KeyboardLayout (props) {
  const { layout, renderKey, scale } = props

  return (
    <div style={{
      position: 'relative',
      transform: `scale(${scale})`,
      transformOrigin: 0
    }}>
      {layout.map((keyLayout, index) => (
        <Placer key={index} keyLayout={keyLayout}>
          {renderKey({ index, keyLayout })}
        </Placer>
      ))}
    </div>
  )
}

KeyboardLayout.propTypes = {
  layout: layoutPropTypes.layout.isRequired,
  renderKey: PropTypes.func.isRequired,
  scale: PropTypes.number
}

KeyboardLayout.defaultProps = {
  scale: 1
}

export default KeyboardLayout
