import classNames from 'classnames'
import { useCallback } from 'react'
import { useDispatch } from 'react-redux'

import { updateSensorBindings } from './layersSlice'
import EncoderBindings from './EncoderBindings'
import styles from './style.module.css'
import Icon from '../../Common/Icon'
import KeyWrapper from '../Keys/KeyWrapper'
import keyStyles from '../Keys/styles.module.css'

export default function SensorBindings ({ layerIndex, layer, sensors }) {
  const dispatch = useDispatch()
  const { parsed: sensorBindings = [] } = layer.properties['sensor-bindings'] || {}

  function handleUpdate (sensorIndex, binding) {
    // TODO: handle sensor bindings list that doesn't match sensor count
    dispatch(updateSensorBindings({
      layerIndex,
      sensorIndex,
      bindings: binding
    }))
  }

  return (
    <div style={{ margin: '0 0 30px' }}>
      <h3>Sensor bindings</h3>
      <div style={{
        display: 'flex',
        flexDirection: 'row',
        gap: '25px'
      }}>
        {sensors.map((sensor, i) => (
          <Sensor
            key={i}
            index={i}
            sensor={sensor}
            binding={sensorBindings[i]}
            canAdd={sensorBindings[i] === undefined && sensorBindings.length === i}
            canDelete={sensorBindings[i] !== undefined && sensorBindings.length === i + 1}
            onUpdate={handleUpdate}
          />
        ))}
      </div>
    </div>
  )
}

function Sensor ({ sensor, binding, index, onUpdate, canAdd, canDelete }) {
  const labelText = sensor.label || sensor.identifier
  const value = binding?.value || '&inc_dec_kp'
  const params = binding?.params || [
    { value: 0, _isPlaceholder: true, params: [] },
    { value: 0, _isPlaceholder: true, params: [] }
  ]

  const handleDelete = useCallback(() => {
    onUpdate(index, undefined)
  }, [index, onUpdate])

  const handleAdd = useCallback(() => {
    onUpdate(index, {
      value: '&inc_dec_kp',
      params: [
        { value: 0, _isPlaceholder: true, params: [] },
        { value: 0, _isPlaceholder: true, params: [] }
      ]
    })
  }, [index, onUpdate])

  return (
    <div className={styles.sensor} data-unbound={!binding}>
      <label style={{ fontVariant: 'small-caps', fontSize: '90%' }}>
        <code>{labelText}</code>
        {canDelete && (
          <Icon
            name="trash-can"
            title="Remove sensor binding from this layer"
            className={styles.deleteSensorBinding}
            onClick={handleDelete}
          />
        )}
      </label>
      {binding && (
        <EncoderBindings
          context="encoders"
          binding={binding || { value, params }}
          onUpdate={bind => onUpdate(index, bind)}
        />
      )}
      {canAdd && (
        <KeyWrapper
          onClick={handleAdd}
          title="Add sensor binding to this layer"
          themeClass={classNames(
            keyStyles.encoder2,
            styles.addSensorBinding
          )}
        >
          <Icon name="add" />
        </KeyWrapper>
      )}
    </div>
  )
}
