import { useContext } from 'react'
import { useDispatch } from 'react-redux'

import { createLayer } from './layersSlice'
import ActionButton from '../../Common/ActionButton'
import { LayoutContext } from '../../Contexts/Layout'

export default function PromptForFirstLayer () {
  const dispatch = useDispatch()
  const { bindingCount } = useContext(LayoutContext)

  return (
    <div style={{ textAlign: 'center' }}>
      <h3>No keymap layers defined</h3>
      <ActionButton onClick={() => dispatch(createLayer({ bindingCount }))}>
        Add layer
      </ActionButton>
    </div>
  )
}
