import compact from 'lodash/compact'
import { useCallback, useState } from 'react'
import { useDispatch } from 'react-redux'
import { mapCharacter } from 'zmk-data/keycode-mappings'

import { updateKeyBinding } from './layersSlice'
import useKeyHandler from './use-key-handler'

const mappings = {
  Alt: 'ALT',
  Control: 'CTRL',
  Shift: 'SHFT',
  Meta: 'GUI',
  ArrowUp: 'UP',
  ArrowDown: 'DOWN',
  ArrowLeft: 'LEFT',
  ArrowRight: 'RIGHT',
  Escape: 'ESC',
  Tab: 'TAB',
  Enter: 'RET',
  Home: 'HOME',
  End: 'END',
  PageDown: 'PG_DN',
  PageUp: 'PG_UP',
  Backspace: 'BSPC',
  Delete: 'DEL'
}

function normalizeKey (event) {
  const mapped = mappings[event.key]
  const prefix = getSidePrefix(event)

  if (!mapped) {
    return null
  }

  return {
    value: '&kp',
    params: [{
      value: prefix + mapped,
      params: []
    }]
  }
}

function getSidePrefix (event) {
  switch (event.location) {
    case 1:
      return 'L'
    case 2:
      return 'R'
    case 0:
    default:
      return ''
  }
}

function getModifiers (event) {
  return compact([
    event.altKey && 'LA',
    event.ctrlKey && 'LC',
    event.metaKey && 'LG',
    event.shiftKey && 'LS'
  ])
}

function getBindFromKey (event) {
  const appliedModifiers = getModifiers(event)
  const mapped = (
    mapCharacter(event.key.toLowerCase()) ||
    normalizeKey(event)
  )

  if (!mapped) {
    return
  }

  return {
    value: mapped.value,
    params: appliedModifiers.reduce((params, modifier) => {
      return [{ value: modifier, params }]
    }, mapped.params)
  }
}

export function useQuickAssignment (layerIndex, layout, defaultStart = false) {
  const dispatch = useDispatch()
  const [isActive, setIsActive] = useState(defaultStart)
  const [bindIndex, setBindIndex] = useState(0)

  const nextBind = useCallback(() => {
    if (bindIndex === layout.length - 1) {
      setBindIndex(0)
      setIsActive(false)
    } else {
      setBindIndex(bindIndex + 1)
    }
  }, [bindIndex, setBindIndex, layout.length, setIsActive])

  const handleKeyPress = useCallback(event => {
    const binding = getBindFromKey(event)

    if (binding) {
      dispatch(updateKeyBinding({
        layerIndex,
        bindIndex,
        binding
      }))

      nextBind()
    }
  }, [layerIndex, bindIndex, nextBind, dispatch])

  useKeyHandler(isActive, handleKeyPress)

  const startQuickAssignment = useCallback(() => {
    setIsActive(true)
  }, [setIsActive])

  const cancelQuickAssignment = useCallback(() => {
    setIsActive(false)
  }, [setIsActive])

  return [
    { isActive, bindIndex },
    { startQuickAssignment, cancelQuickAssignment, setBindIndex }
  ]
}
