import { useCallback, useEffect, useReducer } from 'react'

const INITIAL_STATE = {
  keysHeld: [],
  keysHeldBeforeLastRelease: []
}

function reduceKeyDown (state, action) {
  return {
    keysHeld: [...state.keysHeld, action.event.key],
    keysHeldBeforeLastRelease: []
  }
}

function reduceKeyUp (state, action) {
  const keysHeld = state.keysHeld.filter(key => key !== action.event.key)
  return {
    keysHeld: [],
    keysHeldBeforeLastRelease: keysHeld
  }
}

function useKeyState () {
  return useReducer((state, action) => {
    switch (action.type) {
      case 'KEY_DOWN':
        return reduceKeyDown(state, action)

      case 'KEY_UP':
        return reduceKeyUp(state, action)

      case 'RESET':
        return INITIAL_STATE

      default:
        return state
    }
  }, INITIAL_STATE)
}

/**
 * Handle keydown/keyup events
 *
 * This takes care to avoid firing again upon release of modifiers after the
 * modified key has already been fired.
 *
 * @param {Boolean} isActive - use false to deactivate events
 * @param {Function} onKeyPress - handler for filtered key events
 * @returns null
 */
export default function useKeyHandler (isActive, onKeyPress) {
  const [state, dispatch] = useKeyState()
  const handleKeyDown = useCallback((event) => {
    if (event.repeat) {
      return
    }

    event.preventDefault()
    dispatch({ type: 'KEY_DOWN', event })
  }, [dispatch])

  const handleKeyUp = useCallback((event) => {
    if (state.keysHeldBeforeLastRelease.includes(event.key)) {
      return
    }

    event.preventDefault()
    onKeyPress(event)
    dispatch({ type: 'KEY_UP', event })
  }, [state.keysHeldBeforeLastRelease, onKeyPress, dispatch])

  useEffect(() => {
    if (!isActive) {
      return
    }

    document.body.addEventListener('keydown', handleKeyDown)
    document.body.addEventListener('keyup', handleKeyUp)

    return () => {
      document.body.removeEventListener('keydown', handleKeyDown)
      document.body.removeEventListener('keyup', handleKeyUp)
    }
  }, [isActive, handleKeyDown, handleKeyUp])
}
